@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root{--gray0: #f8f8f8;--gray1: #dbe1e8;--gray2: #b2becd;--gray3: #6c7983;--gray4: #454e56;--gray5: #2a2e35;--gray6: #12181b;--blue: #0084a5;--purple: #a82dd1;--yellow: #fff565;--theme-color: #1890ff;--deep-blue: #001529;--light-gray: #6c798370;--low-opacity-deep-blue: rgba(0, 21, 41, 0.85);--low-opacity-gray: rgba(248, 248, 248, 0.85)}.lightTheme{--bg: var(--gray0);--bg-nav: linear-gradient(to right, var(--gray1), var(--gray3));--bg-dropdown: var(--gray1);--text: var(--gray6);--border-color: var(--blue);--bg-solar: var(--yellow);--social-icon-border: none;--box-shadow: var(--light-gray);--bg-mobile-nav: var(--low-opacity-gray)}.darkTheme{--bg: var(--deep-blue);--bg-nav: linear-gradient(to right, var(--deep-blue), var(--gray6));--bg-dropdown: var(--gray4);--text: var(--gray0);--border-color: var(--purple);--bg-solar: var(--blue);--social-icon-border: var(--gray0);--bg-mobile-nav: var(--low-opacity-deep-blue)}html,body{overflow-x:hidden;color:var(--text) !important}body{margin:0;font-family:"Montserrat",sans-serif !important;font-size:x-large;height:unset !important}p{font-size:16px}h1{color:var(--text) !important}.text-align-left{text-align:left !important}.text-align-center{text-align:center !important}.text-bold{font-weight:bold !important}.ant-divider-horizontal:before,.ant-divider-horizontal:after{border-top:1px solid #1890ff !important;border-top:1px solid var(--theme-color) !important}.big-heading{font-size:clamp(30px,8vw,80px) !important;margin:0 !important}.text-align-center{text-align:center !important}.h-100{height:100% !important}
.img-fluid{width:60%;height:auto;-webkit-filter:drop-shadow(2px -5px 10px gray);filter:drop-shadow(2px -5px 10px gray)}
.skills-component .image-container{max-width:100%}.skills-component .image-container img{height:auto;width:100%}.skills-component .icon-margin-top{margin-top:.5rem !important}.skills-component .icon-card{box-shadow:1.5px 3px 3px 0 rgba(0,0,0,.1);background:var(--bg) !important;transition:background 500ms ease-in-out,color 1000ms ease-in-out}.skills-component .icon-col{margin-bottom:1rem}.skills-component .ant-card-meta{display:flex;justify-content:center;align-items:center}.skills-component .ant-card-body{padding:.5rem 0 !important;display:flex;justify-content:center;align-items:center}.skills-component .ant-card{padding:1rem !important}
.education-item{-webkit-animation-duration:.8s;animation-duration:.8s;-webkit-animation-delay:.3s;animation-delay:.3s;-webkit-animation-name:fadeInUp;animation-name:fadeInUp;box-shadow:0px 10px 20px 4px rgba(0,0,0,.0823529412);text-align:left}.company-name{color:var(--theme-color)}.work-list{list-style-type:none;margin:0;padding:0}.work-list li{position:relative;padding-left:30px;margin-bottom:10px}.work-list li:before{content:"▹";position:absolute;left:0px;color:var(--theme-color);font-size:25px}.image-container{max-width:100%}.image-container img{height:auto;width:100%}p,h1,h2,h3,h4,h5,h6,li{color:var(--text) !important}
.contact-icon{margin-top:24px;text-align:center;width:120px !important;height:120px !important;border:1px solid var(--text);border-radius:50%;line-height:120px !important;border-color:var(--text)}.contact-card{box-shadow:1px 4px 8px rgba(0,0,0,.09);transition:.2s ease-in;margin-bottom:1rem !important;background:var(--bg) !important;transition:background 500ms ease-in-out,color 1000ms ease-in-out,-webkit-transform 200ms ease-in-out;transition:background 500ms ease-in-out,color 1000ms ease-in-out,transform 200ms ease-in-out;transition:background 500ms ease-in-out,color 1000ms ease-in-out,transform 200ms ease-in-out,-webkit-transform 200ms ease-in-out;text-align:center}.contact-card p{padding:0;margin:0}.contact-card:hover{-webkit-transform:translate(0, -15px);transform:translate(0, -15px)}.social-icon{text-align:center !important;height:65px !important;line-height:75px !important;width:65px !important;border-radius:50% !important;transition:-webkit-transform 200ms ease-in !important;transition:transform 200ms ease-in !important;transition:transform 200ms ease-in, -webkit-transform 200ms ease-in !important;padding:0 !important;border:1px solid var(--social-icon-border) !important;box-shadow:1px 1px 5px var(--light-gray) !important}.social-icon i{margin:0 !important;padding:0 !important}.social-icon:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.contact-icon-col{margin-bottom:1rem;display:flex !important;justify-content:center}p,h1,h2,h3,h4,h5,h6,li,i{color:var(--text) !important}
.hamburger{display:none;position:absolute;right:4%;top:40%;z-index:10}.hamburger .hamburger-line{width:30px;height:3px;background:var(--text);position:relative;transition:all .5s ease;display:flex;justify-content:center}.hamburger .hamburger-line::before,.hamburger .hamburger-line::after{content:" ";height:3px;position:absolute;background:inherit;width:100%}.hamburger .hamburger-line::before{top:-10px}.hamburger .hamburger-line::after{top:10px}.navbar{text-align:center !important;background:var(--bg) !important;height:100%;position:relative}.navbar .navbar-nav{list-style-type:none;display:flex;justify-content:center;align-items:center;margin:0;padding:0;height:100%}.navbar .navbar-nav .nav-item{height:100%;margin:0 .5rem}.navbar .navbar-nav .nav-item .nav-link{padding:1rem;height:100%;align-items:center;font-size:14px;justify-content:center;display:flex;color:var(--text);text-decoration:none;transition:color .3s ease-in-out}.navbar .navbar-nav .nav-item .nav-link:hover,.navbar .navbar-nav .nav-item .nav-link.active{background:var(--theme-color);color:var(--gray0)}.light-circle{color:var(--gray0) !important;font-size:1.5rem !important;margin-right:10px}.dark-circle{color:var(--deep-blue) !important;font-size:1.5rem !important;margin-right:10px}@media only screen and (max-width: 600px){.navbar-nav{min-height:100vh !important;width:100% !important;flex-direction:column;justify-content:center;align-items:center;background:var(--bg-mobile-nav);z-index:5;clip-path:circle(0px at 90% -10%);-webkit-clip-path:circle(0px at 90% -10%);transition:all 1s ease-out}.navbar-nav .nav-item{height:unset !important;margin:.2rem !important;width:40% !important}.navbar-nav .nav-item .nav-link{font-weight:bold;text-align:center}.navbar-nav .nav-item:hover,.navbar-nav .nav-item.active{background:unset !important}.navbar-nav.open{clip-path:circle(800px at 90% -10%);-webkit-clip-path:circle(800px at 90% -10%)}.hamburger{display:block}.hamburger.open div{-webkit-transform:rotate(135deg);transform:rotate(135deg)}.hamburger.open div::before,.hamburger.open div::after{top:0;-webkit-transform:rotate(90deg);transform:rotate(90deg)}}
.app-header{padding:0;position:fixed;z-index:5;width:100%}.d-block{display:block !important}.justify-center{justify-content:center !important}.justify-start{justify-content:start !important}.full-height-column{display:flex;justify-content:center;align-items:center}.full-height-row{min-height:calc(100vh - 64px);background-color:var(--bg) !important;padding-bottom:20px;align-items:center}footer{background:var(--bg) !important;color:var(--text) !important;transition:background 500ms ease-in-out,color 1000ms ease-in-out}
